import React, { useEffect, useState, useRef, useMemo, useCallback } from 'react';
import { useGLTF, useAnimations } from '@react-three/drei';

// Mon avatar 3D personnalisÃ© avec animations interactives - OptimisÃ©
const Avatar = React.memo(({ scale = 1, position = [0, 0, 0], animationType = 'marche', onAnimationChange }) => {
  const [isTransitioning, setIsTransitioning] = useState(false);
  const groupRef = useRef();
  
  // MÃ©moisation des modÃ¨les pour Ã©viter les recalculs
  const modelPaths = useMemo(() => ({
    'marche': 'marche en ronde.glb',
    'bonjour': 'bonjour.glb',
    'rumba': 'rumba.glb',
    'hiphop': 'hiphop.glb'
  }), []);
  
  // MÃ©moisation du chemin du modÃ¨le
  const modelPath = useMemo(() => {
    const modelFile = modelPaths[animationType] || modelPaths.marche;
    return process.env.NODE_ENV === 'development' 
      ? `${process.env.PUBLIC_URL || ''}/${modelFile}`
      : `/portfolio-3d/${modelFile}`;
  }, [animationType, modelPaths]);

  const { scene, animations } = useGLTF(modelPath);
  const { actions } = useAnimations(animations, scene);

  // Callback optimisÃ© pour la gestion des animations
  const handleAnimationFinished = useCallback(() => {
    if (onAnimationChange) {
      setTimeout(() => onAnimationChange('marche'), 1000);
    }
  }, [onAnimationChange]);

  useEffect(() => {
    // Marquer le dÃ©but de la transition
    setIsTransitioning(true);

    // Petite pause pour Ã©viter la superposition
    const transitionDelay = setTimeout(() => {
      // ArrÃªter toutes les animations prÃ©cÃ©dentes
      Object.values(actions).forEach(action => {
        if (action) {
          action.stop().reset();
        }
      });

      // Configuration des animations selon le type
      
      Object.keys(actions).forEach(actionName => {
        const action = actions[actionName];
        if (action) {
          // Configuration selon le type d'animation
          if (animationType === 'bonjour') {
            // Animation de salut - jouer une fois puis revenir Ã  la marche
            action.reset()
              .setLoop(2200, 1) // Jouer une fois
              .fadeIn(0.3)
              .play();
            action.timeScale = 1.0;
            
            // Retour automatique Ã  la marche aprÃ¨s l'animation de bonjour
            action.getMixer().addEventListener('finished', handleAnimationFinished);
          } else if (animationType === 'marche') {
            // Animation de marche en boucle
            action.reset()
              .setLoop(2201, 1) // Boucle infinie
              .fadeIn(0.3)
              .play();
            action.timeScale = 1.2;
          } else {
            // Animation rumba, hiphop ou autres - en boucle
            action.reset()
              .setLoop(2201, 1)
              .fadeIn(0.3)
              .play();
            action.timeScale = 1.0;
          }
        }
      });

      // Fin de la transition
      setIsTransitioning(false);
    }, 200); // DÃ©lai de 200ms pour Ã©viter la superposition

    return () => clearTimeout(transitionDelay);
  }, [actions, animationType, onAnimationChange]);

  return (
    <group ref={groupRef} visible={!isTransitioning}>
      <primitive 
        object={scene} 
        scale={scale} 
        position={position}
      />
    </group>
  );
});

// PrÃ©chargement des modÃ¨les optimisÃ©
const preloadModels = () => {
  const models = ['marche en ronde.glb', 'bonjour.glb', 'marche.glb'];
  models.forEach(model => {
    const path = process.env.NODE_ENV === 'development' 
      ? `${process.env.PUBLIC_URL || ''}/${model}`
      : `/portfolio-3d/${model}`;
    useGLTF.preload(path);
  });
};

preloadModels();

export default Avatar;